import React from 'react';

interface AppProps {
}

function App(props: AppProps) {
  function handleClick() {
    navigator.clipboard.writeText('https://www.verishop.com/live-shop/293519').then(() => {
      console.log('Async: Copying to clipboard was successful!');
      window.location.href = 'https://apps.apple.com/us/app/verishop-social-shopping/id1459442699';
    }, function(err) {
      console.error('Async: Could not copy text: ', err);
    });
  }

  return (
    <div className="App">
      <button onClick={handleClick}>
        Install App and Join Livestream
      </button>
    </div>
  );
}

export default App;
